/* global gapi */
import {getMessageRequest, getMessageSuccess} from '../constants'

export const getMessage = (id) => dispatch => {
  dispatch(getMessageRequest());
  gapi.client.gmail.users.messages.get({
    userId: 'me',
    id
  }).then(({result}) => {
    dispatch(getMessageSuccess(result));
  })
};


