import {GET_MESSAGE_REQUEST, GET_MESSAGE_SUCCESS} from "../constants";

const initialState = {
  isLoading: false,
  message: {}
};

export default function inboxReducer(state = initialState, action) {
  switch (action.type) {
    case GET_MESSAGE_REQUEST:
      return {
        ...state,
        isLoading: true
      };
    case GET_MESSAGE_SUCCESS:
      return {
        ...state,
        message: action.payload,
        isLoading: false
      };
    default:
      return state;
  }
}


